import React, { useState, useEffect } from 'react'
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
  CButton,
  CButtonGroup,
} from '@coreui/react'

import { useHistory } from 'react-router-dom'

import slugify from 'slugify'

import Loader from '../../components/Loader'
import Message from '../../components/Message'
import { Authentification } from '../../components/index'

import {
  getRestaurants,
  deleteRestaurant,
} from '../../provider/restaurantFetch'

import { variable } from '../../configs/variable'

const Restaurants = () => {
  /** State */
  const [restaurants, setRestaurants] = useState(null)
  const [error, setError] = useState(null)

  /** Hook Route */
  let history = useHistory()

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log('Run once, when the component mounts')

    async function fetch() {
      const resp = await getRestaurants()

      resp?.status ? setRestaurants([...resp?.data]) : setError(resp?.error)
    }

    fetch()
  }, [])

  const deleteFunction = async (id) => {
    const resp = await deleteRestaurant(id)

    if (resp.status) {
      setRestaurants((old) => old.filter((u) => u._id !== id))
    }
  }

  const handleNavigation = (id) => {
    history.push(`/detail-restaurant/${id}`)
  }

  const addItem = (id) => {
    history.push(`/ajout-food?restaurant=${id}`)
  }

  const discoveryTable = (id) => {
    history.push(`/discovery-table/${id}`)
  }

  const ajout = () => {
    history.push(`/ajout-restaurant`)
  }

  const edit = (id) => {
    history.push(`/ajout-restaurant/${id}`)
  }

  const lien = (url) => {
    // window.location.href = "https://www.w3schools.com"
    window.open(url, '_blank')
  }

  if (restaurants === null) {
    return <Loader />
  }

  if (error) {
    return <Message> {error}</Message>
  }

  return (
    <CRow>
      <Authentification />
      <CCol xs={12}>
        <CCard className="mb-4">
          <CCardHeader>
            <CRow>
              <CCol sm={5}>
                <strong>Listes des Restaurants</strong>
              </CCol>
              <CCol sm={7}>
                <CButtonGroup className="float-end me-3">
                  <CButton
                    color="outline-secondary"
                    className="mx-0"
                    onClick={() => ajout()}
                  >
                    Ajout
                  </CButton>
                </CButtonGroup>
              </CCol>
            </CRow>
          </CCardHeader>
          <CCardBody>
            <CTable>
              <CTableHead>
                <CTableRow>
                  <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Commercant</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Lien</CTableHeaderCell>
                  <CTableHeaderCell scope="col">Actions</CTableHeaderCell>
                </CTableRow>
              </CTableHead>
              <CTableBody>
                {restaurants.length > 0 ? (
                  restaurants.map((restaurant) => (
                    <CTableRow key={restaurant._id}>
                      <CTableDataCell>{restaurant.name}</CTableDataCell>
                      <CTableDataCell>
                        {restaurant?.user?.firstName}{' '}
                        {restaurant?.user?.lastName}
                      </CTableDataCell>
                      <CTableDataCell>
                        <CButton
                          color={'primary'}
                          className="m-1"
                          onClick={() =>
                            lien(
                              `${
                                variable.env === 'local'
                                  ? variable.endpointFront
                                  : variable.endpointProd
                              }/${slugify(restaurant.name, {
                                lower: true,
                              })}?id=${restaurant._id}`
                            )
                          }
                        >
                          {' '}
                          Lien{' '}
                        </CButton>
                      </CTableDataCell>
                      <CTableDataCell>
                        <CButton
                          color={'primary'}
                          className="m-1"
                          onClick={() => handleNavigation(restaurant._id)}
                        >
                          {' '}
                          Voir{' '}
                        </CButton>
                        <CButton
                          color={'primary'}
                          className="m-1"
                          onClick={() => discoveryTable(restaurant._id)}
                        >
                          {' '}
                          DiscoveryTable{' '}
                        </CButton>
                        <CButton
                          color={'primary'}
                          className="m-1"
                          onClick={() => addItem(restaurant._id)}
                        >
                          {' '}
                          Ajout Items{' '}
                        </CButton>
                        <CButton
                          color={'secondary'}
                          className="m-1"
                          onClick={() => edit(restaurant._id)}
                        >
                          {' '}
                          Editer{' '}
                        </CButton>
                        <CButton
                          color={'danger'}
                          onClick={() => deleteFunction(restaurant._id)}
                        >
                          {' '}
                          Supprimer{' '}
                        </CButton>
                      </CTableDataCell>
                    </CTableRow>
                  ))
                ) : (
                  <h3>{'NO Items'}</h3>
                )}
              </CTableBody>
            </CTable>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  )
}

export default Restaurants
